import * as tools from "../utils/tools";
import { Point } from "../utils/point";
const canvasSketch = require("canvas-sketch");
const p5 = require("p5");

const _ = new p5();

const settings = {
	p5: true,
	dimensions: [2048, 2048],
	animate: true,
};

window.preload = () => {};

canvasSketch(({ width, height }) => {
	const p = new Point(width / 2, height / 2, 100, tools.rgbRandomPastelColor());

	return ({ context, width, height }) => {
		context.fillRect(0, 0, width, height);

		const dx = mouseX - p.x;
		const dy = mouseY - p.y;
		p.setVector(dx / 10, dy / 10);

		p.move(width, height);
		p.draw(context);
	};
}, settings);
